// nesting the app
class DecisionApp extends React.Component {
    render() {
        const title = 'Decision 1';
        const subtitle = 'Webentwicklung 1';
        const options = ['HTML', 'CSS', 'JavaScript'];
        
        // class based components
        // instances of react components

        return (
        
        <div>
                <Header title={title} subtitle={subtitle}/>
                <Action />
                <Options options={options} />
                <AddOption />
            </div>
        );
    }
}

// create component property
class Header extends React.Component {
    // render method
    // returns jsx
    render() {
        return (
            <div>
                <h1>{this.props.title}</h1>
                <h2>{this.props.subtitle}</h2>
            </div>
        );
    }
}

// React Components start with big letter
// add onClick event handler

class Action extends React.Component {
    handlePick() {
        alert('handlePick');
    }
    render() {
        return (
            <div>
                <button onClick={this.handlePick}>TODO LIST</button>
            </div>
        );
    }
}

// addRemoveAll button
// setup handleremoveall -> alert some message
// setup onclick to fire the method


class Options extends React.Component {
    // method
    handleRemoveAll() {
        alert('removeAll');
    }


    render() {
        return (
            <div>
                
                <button onClick={this.handleRemoveAll}>REMOVE ALL</button>

                Options
                <Option />
                <p>render of the options length</p>
                <p>{this.props.options.length}</p>
                <p>list render of the options</p>
                <ol>
                    {
                        this.props.options.map((option) => <Option key={option} optionText={option} />)
                        // this.props.options.map((option) => <li key={option}>{option}</li>)
                    }
                </ol>




            </div>
        );
    }
}

class Option extends React.Component {
    render() {
        return (
            <div>
                {this.props.optionText}
            </div>
        );
    }
}

// trim() clear empty spac bevore and after string
class AddOption extends React.Component {
    handleAddOption(e) {
        e.preventDefault();

        const option = e.target.elements.option.value.trim();

        if (option) {
            alert(option);
        }
    }
    render() {
        return (
            <div>
                <form onSubmit={this.handleAddOption}>
                    <input type="text" name="option" />
                    <button>ADD OPTION</button>
                  </form>
            </div>
        );
    }
}



ReactDOM.render(<DecisionApp />, document.getElementById('app'));